import {getComments,postComments} from '@/api/home'
export default {
    middleware:'authtic',
    props:{
        articleObj:{
        type:Object,
        required:true,
        default:{}
        }
    },
    data(){
       return{
           comment:[],
           value:''
       }
    },
    name:"ArticleComment",
  async  mounted(){
       this.comment =  await getComments(this.articleObj.slug)
    },
    methods:{
    async post(){
        await postComments(this.articleObj.slug,{comment:{body:this.value}})
     }
    }
    
}
